const getters = {
  LoginError: state => state.loginx.LoginError,
  LoginFlag: state => state.loginx.LoginFlag,
  Action: state => state.loginx.Action,
  list: state => state.loginx.list,
  ListErrorMsg: state => state.loginx.ListErrorMsg,
  AddServerErrorMsg: state => state.loginx.AddServerErrorMsg
}
export default getters




